import React, { FC } from 'react'
import { Button } from '@mui/material'
import { useActions } from '../../hooks/action'
import { IEntry } from '../../types/types'

interface IProps {
    removeObject: IEntry | null
    removeArray: string | null
}

const ButtonsModal: FC<IProps> = ({ removeObject, removeArray }) => {
    const { isToOpenModal, removeThisObject, removeEntry, removeThisArray, removeAllArray } = useActions()

    const clear: () => void = () => {
        isToOpenModal(false)
        removeThisObject(null)
        removeThisArray(null)
    }

    const onCancel: () => void = () => clear()
    const onRemove: (id: string) => void = id => removeEntry(id) && clear()
    const onRemoveAll: (id: string) => void = id => removeAllArray(id) && clear()

    return (
        <div className='flex justify-center pt-5 items-center'>
            <Button onClick={onCancel} size='small' variant='outlined' color='primary' sx={{ mr: '10px' }}>
                Cancel
            </Button>
            {removeObject && (
                <Button onClick={() => onRemove(removeObject.id)} size='small' variant='contained' color='primary'>
                    Yes
                </Button>
            )}
            {removeArray && (
                <Button onClick={() => onRemoveAll(removeArray)} size='small' variant='contained' color='primary'>
                    Yes
                </Button>
            )}
        </div>
    )
}

export default ButtonsModal
